.switch {
  width: 60px;
  height: 34px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #c5e4f2;
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: 0 0 10px 1px #bdc3c7;
}

.slider:before {
  content: "";
  background-color: #fff;
  width: 26px;
  height: 26px;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: #1a567e;
  box-shadow: 0 0 10px 1px #2c3e50;
}

input:focus + .slider {
  box-shadow: 0 0 10px 1px #2c3e50;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
